/* Setting up fonts */
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswald.woff") format("woff");
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-semibold.woff") format("woff");
  font-weight: 600;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-bold.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "lato";
  src: url("../fonts/lato-regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "lato";
  src: url("../fonts/lato-bold.woff") format("woff");
  font-weight: 700;
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: hsl(215, 92%, 15%);
  --secondary-color: hsl(41, 75%, 42%);
  --title-font: "oswald";
  --body-font: "lato";
}

h1,
h2,
h3 {
  font-family: "oswald";
  letter-spacing: 1.4;
}

p,
li,
span {
  font-family: "lato";
}

/* Remove default margin */
/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.6;
  max-width: 100%;
  overflow-x: hidden;
  position: relative;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
.section-title {
  display: inline-block;
  position: relative;
  text-align: center;
  font-size: 4rem;
  font-weight: 900;
  color: var(--primary-color);
  letter-spacing: 2;
  margin-bottom: 1em;
}
.section-title:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  bottom: -4px;
  left: 0;
  background-color: var(--secondary-color);
}

.hero {
  height: 92vh;
  background: url("../img/hero-bg.webp"), rgba(0, 0, 0, 0.7);
  background-blend-mode: darken;
  background-size: cover;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.hero .row {
  flex-direction: column;
  margin-bottom: 8em;
  text-align: center;
}
.hero .row .tag-line {
  font-size: 4rem;
  line-height: 1.2;
  color: var(--secondary-color);
  text-align: center;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
.hero .row .admission {
  margin-top: 2em;
  text-transform: uppercase;
  padding: 0.5em 1em;
  text-decoration: none;
  background: var(--secondary-color);
  border: none;
  border-radius: 5px;
  transition: all 300ms;
}
.hero .row .admission a {
  font-family: var(--title-font);
  font-weight: 800;
  text-decoration: none;
  letter-spacing: 1.4;
  font-size: 1.3rem;
  color: var(--primary-color);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}
.hero .row .admission:hover {
  background: #e2ac36;
  transition: all 300ms;
}

.why-we {
  padding: 2em 0;
}
.why-we .why-we-title-container {
  text-align: center;
}
.why-we .why-we-title-container .why-we-title {
  letter-spacing: 1;
}
.why-we .why-we-container {
  text-align: center;
}
.why-we .why-we-container .why-col {
  margin-bottom: 1.5em;
  padding: 0.5em 0;
  background: var(--secondary-color);
  border-radius: 5px;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
}
.why-we .why-we-container .why-col h2 {
  font-size: 3rem;
  color: var(--primary-color);
}
.why-we .why-we-container .why-col p {
  font-weight: 700;
}

.collage {
  padding: 2em 0;
}
.collage .collage-title-container {
  text-align: center;
}
.collage .collage-container {
  display: grid;
  grid-gap: 5px;
  align-items: stretch;
  grid-template-areas: "img1 img1" "img4 img5" "img2 img2" "img6 img7" "img3 img3";
}
.collage .collage-container .collage-image {
  border: 2px solid #efd18f;
  border-radius: 2px;
}
.collage .collage-container .collage-image-1 {
  grid-area: img1;
}
.collage .collage-container .collage-image-2 {
  grid-area: img2;
}
.collage .collage-container .collage-image-3 {
  grid-area: img3;
}
.collage .collage-container .collage-image-4 {
  grid-area: img4;
}
.collage .collage-container .collage-image-5 {
  grid-area: img5;
}
.collage .collage-container .collage-image-6 {
  grid-area: img6;
}
.collage .collage-container .collage-image-7 {
  grid-area: img7;
}

.testimony {
  padding: 2em 0;
  position: relative;
  z-index: 3;
  background: #fff;
}
.testimony .testimony-title-container {
  text-align: center;
}
.testimony .testimony-container .student-testimony {
  margin: 0 1em;
  margin-bottom: 1em;
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
.testimony .testimony-container .student-testimony .student-detail {
  background: var(--secondary-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em;
  border-radius: 10px 10px 0 0;
}
.testimony .testimony-container .student-testimony .student-detail .student-image {
  width: 30%;
  border-radius: 1000px;
}
.testimony .testimony-container .student-testimony .student-detail .name-branch {
  text-align: right;
  color: var(--primary-color);
  font-family: var(--title-font);
  font-weight: 800;
  letter-spacing: 0.8;
  font-size: 1.1rem;
  line-height: 1.2;
}
.testimony .testimony-container .student-testimony .student-detail .name-branch .student-name {
  font-family: var(--title-font);
  margin-bottom: 0.5em;
}
.testimony .testimony-container .student-testimony .student-detail .name-branch .student-field {
  font-family: var(--title-font);
}
.testimony .testimony-container .student-testimony .testimony-detail {
  background: #eee;
  border-radius: 0 0 10px 10px;
  padding: 1em 1em;
}

.notification {
  padding: 2em 0;
}
.notification .notification-title-container {
  text-align: center;
}
.notification .notification-title-container h1.notification-title {
  margin-bottom: 0.5em;
}
.notification .notification-container .news {
  width: 100%;
}
.notification .notification-container .news h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 0.3em;
  font-weight: 900;
  color: var(--primary-color);
}
.notification .notification-container .news ul {
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.3);
  background: #eee;
  padding: 1em 2em;
  border-radius: 10px;
}
.notification .notification-container .news ul .news-item {
  margin: 0.5em 0;
}
.notification .notification-container .news ul .news-item a {
  text-decoration: none;
  color: black;
}
.notification .notification-container .events {
  width: 100%;
}
.notification .notification-container .events h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 0.3em;
  font-weight: 900;
  color: var(--primary-color);
}
.notification .notification-container .events ul {
  box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.3);
  background: #eee;
  padding: 1em 2em;
  border-radius: 10px;
}
.notification .notification-container .events ul .event-item {
  margin: 0.5em 0;
}
.notification .notification-container .events ul .event-item a {
  text-decoration: none;
  color: black;
}

.footer {
  background: var(--primary-color);
  padding: 2em 0;
  position: relative;
  z-index: 2;
}
.footer .footer-container .footer-col .footer-col-title {
  font-size: 1.5rem;
  color: #fff;
  margin: 1em 0 0.5em 0;
}
.footer .footer-container .footer-col .footer-link {
  text-decoration: none;
  color: #fff;
}
.footer .social-container {
  justify-content: center;
  text-align: center;
  margin-top: 2em;
}
.footer .social-container a {
  width: 100%;
  text-decoration: none;
}
.footer .social-container a img {
  width: 10%;
  margin: 0 0.3em;
}

.disclaimer {
  background-color: #021631;
}
.disclaimer .disclaimer-container {
  text-align: center;
  color: #fff;
  padding: 0.5em 0;
}

.enquiry {
  position: fixed;
  top: 30%;
  right: 0;
  z-index: 1;
  transform: rotate(270deg);
  transform-origin: right bottom;
}
.enquiry .enquiry-button {
  padding: 0.2em 1em;
  margin: 0 0.5em;
  display: inline-block;
  background: var(--primary-color);
  color: #fff;
  font-family: var(--title-font);
  border-radius: 5px 5px 0 0;
}
.enquiry .enquiry-button a {
  text-decoration: none;
  color: #fff;
}

@media (min-width: 1000px) {
  .hero .row .tag-line {
    font-size: 4rem;
  }

  .why-we .why-we-container {
    display: flex;
    gap: 1em;
    justify-content: space-between;
    align-items: center;
  }
  .why-we .why-we-container .why-col {
    padding: 1.5em 0;
    width: 100%;
  }
  .why-we .why-we-container .why-col h2 {
    font-size: 3rem;
    margin-bottom: 0.2em;
  }

  .collage .collage-container {
    display: grid;
    grid-gap: 10px;
    grid-template-areas: "img1 img1 img3" "img4 img5 img6" "img7 img2 img2";
  }
  .collage .collage-container .collage-image {
    border: 4px solid #efd18f;
    border-radius: 6px;
  }
  .collage .collage-container .collage-image-1 {
    grid-area: img1;
  }
  .collage .collage-container .collage-image-2 {
    grid-area: img2;
  }
  .collage .collage-container .collage-image-3 {
    grid-area: img3;
  }
  .collage .collage-container .collage-image-4 {
    grid-area: img4;
  }
  .collage .collage-container .collage-image-5 {
    grid-area: img5;
  }
  .collage .collage-container .collage-image-6 {
    grid-area: img6;
  }
  .collage .collage-container .collage-image-7 {
    grid-area: img7;
  }

  .notification .notification-container {
    align-items: flex-start;
    gap: 2em;
  }
  .notification .notification-container .news {
    width: 100%;
  }
  .notification .notification-container .events {
    width: 100%;
  }

  .footer {
    background: var(--primary-color);
    padding: 2em 0;
  }
  .footer .footer-container {
    gap: 3em;
    align-items: flex-start;
  }
  .footer .footer-container .footer-col .footer-col-title {
    color: #fff;
  }
  .footer .footer-container .footer-col .footer-link {
    text-decoration: none;
    color: #fff;
  }
  .footer .social-container {
    justify-content: center;
    text-align: center;
    margin-top: 2em;
  }
  .footer .social-container a {
    width: 100%;
    text-decoration: none;
  }
  .footer .social-container a img {
    width: 20%;
    margin: 0 0.3em;
  }
}
.container {
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
}

.header {
  background-color: white;
  padding: 0.5em 0;
  text-align: center;
  position: relative;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.25);
}
.header .nav-toggle {
  cursor: pointer;
  border: 0;
  width: 3em;
  height: 3em;
  padding: 0em;
  border-radius: 50%;
  transition: opacity 250ms ease;
  position: absolute;
  left: 0;
  background-color: white;
}
.header .nav-toggle::focus, .header .nav-toggle::hover {
  opacity: 0.75;
}
.header .nav-toggle .hamburger {
  width: 50%;
  position: relative;
  display: block;
  margin: 0 auto;
  height: 3px;
  border-radius: 8px;
  background: var(--primary-color);
}
.header .nav-toggle .hamburger::before, .header .nav-toggle .hamburger::after {
  display: block;
  margin: 0 auto;
  height: 3px;
  border-radius: 8px;
  content: "";
  background: var(--primary-color);
  transition: width 250ms;
}
.header .nav-toggle .hamburger::before {
  width: 120%;
  transform: translateY(-8px);
}
.header .nav-toggle .hamburger::after {
  width: 120%;
  transform: translateY(5px);
}
.header .nav-toggle .hamburger-open::after, .header .nav-toggle .hamburger-open::before {
  width: 80%;
  transition: width 250ms;
}
.header .logo .logo-image {
  width: 140px;
}
.header .nav {
  max-height: 0;
  overflow: hidden;
  transition: max-height 250ms;
}
.header .nav .nav-list {
  margin-top: 1em;
}
.header .nav .nav-list .nav-item {
  list-style-type: none;
  margin: 0.5em 0 0.5em 0;
}
.header .nav .nav-list .nav-item .nav-link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--primary-color);
  font-family: var(--title-font);
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
  letter-spacing: 0.5;
}
.header .nav .nav-list .nav-item .nav-link:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: var(--primary-color);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.header .nav .nav-list .nav-item .nav-link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

@media (min-width: 1000px) {
  .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header .nav-toggle {
    visibility: hidden;
  }
  .header .nav {
    max-height: 100vh;
    overflow: auto;
  }
  .header .nav .nav-list {
    display: flex;
    margin: 0;
    align-items: center;
  }
  .header .nav .nav-list .nav-item {
    margin-left: 2em;
  }
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px var(--secondary-color);
  border-radius: 0px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #aaaaaa;
  border-radius: 0px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #888888;
}