/* Setting up fonts */
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswald.woff") format("woff");
}

@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-semibold.woff") format("woff");
  font-weight: 600;
}

@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-bold.woff") format("woff");
  font-weight: 700;
}

@font-face {
  font-family: "lato";
  src: url("../fonts/lato-regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: "lato";
  src: url("../fonts/lato-bold.woff") format("woff");
  font-weight: 700;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: hsl(215, 92%, 15%);
  --secondary-color: hsl(41, 75%, 42%);
  --title-font: "oswald";
  --body-font: "lato";
}

h1,
h2,
h3 {
  font-family: "oswald";
  letter-spacing: 1.4;
}

p,
li,
span {
  font-family: "lato";
  // letter-spacing: 1;
}

